.links_container{
    display: flex;
    align-items: center;
}

.post_container {
  resize: none;
  overflow-x: scroll;
  white-space: nowrap;
  width: calc(100% - 52px);
  height: 32px;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;

  color: #8d8d8d;
  border: 1px solid #8d8d8d;
  border-radius: 12px;
  margin-top: 16px;
  margin-bottom: 4px;
  margin-right: 8px;
  padding: 15px 24px 5px 24px;
}